import axios from 'axios';

// Constante para la URL base del API
// En Docker, la ruta ser /api segn la configuracin de Nginx
const API_URL = process.env.REACT_APP_API_URL || '/api';
console.log('API URL configurada:', API_URL);

// Instancia de axios con configuracin comn
const apiClient = axios.create({
  baseURL: API_URL,
  timeout: 30000,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Interceptor para manejar errores globalmente
apiClient.interceptors.response.use(
  (response) => response,
  (error) => {
    console.error('Error en la solicitud API:', error);
    // Manejo centralizado de errores
    if (error.response) {
      // El servidor respondi con un cdigo de estado fuera del rango de 2xx
      console.error('Respuesta del servidor con error:', error.response.data);
      if (error.response.status === 401) {
        // Manejar redireccin de autenticacin si es necesario
        // window.location.href = '/login';
      }
    } else if (error.request) {
      // La solicitud se realiz pero no se recibi respuesta
      console.error('No se recibi respuesta del servidor');
    }
    return Promise.reject(error);
  }
);

