import React, { useState, useEffect } from 'react';
import './AirQualityMonitoring.css';

function AirQualityMonitoring() {
  const [airQualityData, setAirQualityData] = useState({
    aqi: 45,
    pm25: 12.3,
    pm10: 25.7,
    co: 0.8,
    no2: 15.2,
    so2: 3.1,
    o3: 42.6,
    lastUpdated: new Date().toLocaleString()
  });

  // Datos simulados para un grfico
  const [historicalData, setHistoricalData] = useState([
    { date: '01/05', aqi: 42 },
    { date: '02/05', aqi: 38 },
    { date: '03/05', aqi: 47 },
    { date: '04/05', aqi: 51 },
    { date: '05/05', aqi: 45 },
    { date: '06/05', aqi: 39 },
    { date: '07/05', aqi: 35 },
  ]);

  // Funcin para determinar la categora de calidad del aire
  const getAirQualityCategory = (aqi) => {
    if (aqi <= 50) return { category: 'Buena', color: '#00e400' };
    if (aqi <= 100) return { category: 'Moderada', color: '#ffff00' };
    if (aqi <= 150) return { category: 'Daina para grupos sensibles', color: '#ff7e00' };
    if (aqi <= 200) return { category: 'Daina', color: '#ff0000' };
    if (aqi <= 300) return { category: 'Muy daina', color: '#99004c' };
    return { category: 'Peligrosa', color: '#7e0023' };
  };

  const airQualityInfo = getAirQualityCategory(airQualityData.aqi);

  return (
    <div className="air-quality-container">
      <h1>Monitoreo de Calidad del Aire</h1>
      
      <div className="dashboard-cards">
        <div className="card aqi-card">
          <h2>ndice de Calidad del Aire (AQI)</h2>
          <div className="aqi-display" style={{ backgroundColor: airQualityInfo.color }}>
            <span className="aqi-value">{airQualityData.aqi}</span>
            <span className="aqi-category">{airQualityInfo.category}</span>
          </div>
          <p>ltima actualizacin: {airQualityData.lastUpdated}</p>
        </div>
        
        <div className="card pollutants-card">
          <h2>Contaminantes</h2>
          <div className="pollutants-grid">
            <div className="pollutant-item">
              <h3>PM2.5</h3>
              <p>{airQualityData.pm25} g/m</p>
            </div>
            <div className="pollutant-item">
              <h3>PM10</h3>
              <p>{airQualityData.pm10} g/m</p>
            </div>
            <div className="pollutant-item">
              <h3>CO</h3>
              <p>{airQualityData.co} ppm</p>
            </div>
            <div className="pollutant-item">
              <h3>NO</h3>
              <p>{airQualityData.no2} ppb</p>
            </div>
            <div className="pollutant-item">
              <h3>SO</h3>
              <p>{airQualityData.so2} ppb</p>
            </div>
            <div className="pollutant-item">
              <h3>O</h3>
              <p>{airQualityData.o3} ppb</p>
            </div>
          </div>
        </div>
      </div>

      <div className="card history-card">
        <h2>Historial AQI (7 das)</h2>
        <div className="chart-container">
          {historicalData.map((data, index) => (
            <div 
              key={index} 
              className="chart-bar" 
              style={{ 
                height: `${data.aqi * 2}px`,
                backgroundColor: getAirQualityCategory(data.aqi).color 
              }}
              title={`${data.date}: AQI ${data.aqi}`}
            >
              <span className="bar-value">{data.aqi}</span>
              <span className="bar-date">{data.date}</span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default AirQualityMonitoring; 